import refresh from '../../components/refresh/refresh.vue'
import share from '../../components/share-module/share-module.vue'
export default {
	components: {
		refresh,
		share
	},
	props: {
		statusColor: {
			type: String,
			default: '#FFFFFF'
		}
	},
	data() {
		return {
			// swiperHeight: 0,
			tagIndex: 0, // 顶部选中标签
			Hotdata: [], // 热门数据
			Hotpages: 1, // 热门数据页数
			tabIndex: 0, // sweiper所在页
			pages: [1, 1, 1, 1, 1, 1, 1, 1, 1], //第几个swiper的第几页
			// tab项 - 数据
			tabList: [
				// {
				// 	name: '推荐',
				// 	id: 'tuijian',
				// 	data: [4, 5, 6]
				// }
			],
			shareType: false,
			dataUrl: '/login/selectAppArticlePage', // 分页数据链接
		}
	},
	// created () { let that = this },
	onLoad() {
		this.getEssayType()
		// this.getEssayData()
	},
	methods: {
		// tab选择
		tabSelect(index, e) {
			if (this.tabIndex === index) return false
			this.tabIndex = index
		},
		// swiper滑动
		swiperChange(e) {
			let {
				current
			} = e.target
			this.tabIndex = current
		},
		// 分页数据(触底)请求事件 当然刷新和其他请求可以写一起 多一层判断。
		isRequest() {
			let that = this;
			return new Promise((resolve, reject) => {
				// 热门请求
				if (this.tagIndex === 0) {
					this.Hotpages++;
					let data = {
						articleId: 1,
						pageCount: this.Hotpages,
						pageSize: 10
					}
					this.$axios.request({
						url: that.dataUrl,
						method: 'post',
						data: data
					}).then(function(res) {
						var dataArr = res.data.data.dataList;
						// let newData = new Array();
						let newData = [];
						for (var i = 0; i < dataArr.length; i++) {
							// 热门数据
							newData.push(dataArr[i])
						}
						if (dataArr.length <= 0) {
							that.Hotpages--; // 数据为空时，回退一页
						}
						uni.hideLoading()
						resolve(newData)
					})
				}
				// 推荐请求
				else if (this.tagIndex === 1) {
					this.pages[this.tabIndex]++
					let data = {
						articleId: 1,
						pageCount: this.pages[this.tabIndex],
						pageSize: 10
					}
					this.$axios.request({
						url: that.dataUrl,
						method: 'post',
						data: data
					}).then(function(res) {
						var dataArr = res.data.data.dataList;
						let newData = [];
						for (var i = 0; i < dataArr.length; i++) {
							// 推荐数据分类
							if (dataArr[i].articleTypeId == that.tabList[that.tabIndex].id) {
								newData.push(dataArr[i])
							}
						}
						if (dataArr.length <= 0) {
							that.pages[that.tabIndex]--; // 数据为空时，回退一页
						}
						uni.hideLoading()
						resolve(newData)
					})
				}
				// 	uni.showToast({
				// 		icon: 'none',
				// 		title: `请求第${that.tabIndex + 1}个导航栏的第${that.pages[that.tabIndex]}页数据成功`
				// 	})
			})
		},
		// 加载更多
		lower: function(e) {
			console.log(`加载${this.tabIndex}`) //tabIndex表示当前所在页数 根据当前所在页数发起请求并带上page页数
			uni.showLoading({
				title: '加载中...',
				mask: true
			})
			this.isRequest().then(res => {
				if (this.tagIndex === 0) {
					// 热门数据
					if (res.length <= 0) {
						uni.showToast({
							icon: 'none',
							title: `没有更多数据了!`
						})
						return false;
					}
					this.Hotdata = this.Hotdata.concat(res)
					console.log(this.Hotdata)
					this.$forceUpdate()
				}
				else if (this.tagIndex === 1) {
					// 推荐数据
					if (res.length <= 0) {
						uni.showToast({
							icon: 'none',
							title: `没有更多数据了!`
						})
						return false;
					}
					let tempList = this.tabList
					tempList[this.tabIndex].data = tempList[this.tabIndex].data.concat(res)
					console.log(tempList)
					this.tabList = tempList
					this.$forceUpdate() //二维数组，开启强制渲染
				}
			})
		},
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e)
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e)
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e)
		},
		isRefresh() {
			let that = this;
			if (this.tagIndex === 0) {
				let data = {
					articleId: 1,
					pageCount: 1,
					pageSize: 10
				}
				this.$axios.request({
					url: that.dataUrl,
					method: 'post',
					data: data
				}).then(function(res) {
					var dataArr = res.data.data.dataList
					that.Hotdata = [];
					for (var i = 0; i < dataArr.length; i++) {
						// 热门数据
						that.Hotdata.push(dataArr[i])
					}
					console.log("热门数据(刷新): " + that.Hotdata.length);
					uni.showToast({
						icon: 'success',
						mask: true, // 显示透明蒙层，防止触摸穿透
						title: '刷新成功'
					})
					that.$refs.refresh.endAfter() //刷新结束调用
				})
			} else if (this.tagIndex === 1) {
				let data = {
					articleId: 1,
					pageCount: 1,
					pageSize: 10
				}
				this.$axios.request({
					url: that.dataUrl,
					method: 'post',
					data: data
				}).then(function(res) {
					var dataArr = res.data.data.dataList
					that.tabList[that.tabIndex].data = [];
					for (var i = 0; i < dataArr.length; i++) {
						// 推荐数据分类
						if (dataArr[i].articleTypeId == (that.tabIndex + 1)) {
							that.tabList[that.tabIndex].data.push(dataArr[i])
						}
					}
					console.log("推荐数据(刷新): " + that.Hotdata.length);
					uni.showToast({
						icon: 'success',
						mask: true, // 显示透明蒙层，防止触摸穿透
						title: '刷新成功'
					})
					that.$refs.refresh.endAfter() //刷新结束调用
				})
			}
		},
		// 获取所有文章类型
		getEssayType: function() {
			let that = this;
			// 显示 loading 提示框
			uni.showLoading({
				title: 'loading...'
			})
			let url = '/login/selectAllAppArticleTyp'
			this.$axios.request({
				url: url,
				method: 'post',
			}).then(function (res) {
				var typeArr = res.data.data;
				for (var i = 0; i < typeArr.length; i++) {
					var item = {};
					item.name = typeArr[i].articleTypeName;
					item.id = typeArr[i].articleTypeId;
					item.data = [];
					that.tabList.push(item);
				}
				// console.log(that.tabList);
				that.getEssayData()
				
				uni.hideLoading()
			})
		},
		// 获取数据
		getEssayData: function() {
			let that = this;
			// 显示 loading 提示框
			// uni.showLoading({
			// 	title: 'loading...'
			// })
			let data = {
				articleId: 1,
				pageCount: 1,
				pageSize: 10
			}
			this.$axios.request({
				url: that.dataUrl,
				method: 'post',
				data: data
			}).then(function(res) {
				var dataArr = res.data.data.dataList
				console.log(dataArr)
				for (var i = 0; i < dataArr.length; i++) {
					// 热门数据
					that.Hotdata.push(dataArr[i])
					// 推荐数据分类
					for (var j = 0; j < that.tabList.length; j++) {
						if (dataArr[i].articleTypeId == that.tabList[j].id) {
							that.tabList[j].data.push(dataArr[i])
						}
					}
				}
				console.log("热门数据: " + that.Hotdata.length);
				console.log("推荐数据: " + that.tabList);
				
				// uni.hideLoading()
			})
		},
		// 详情页
		openDetail: function(e) {
			var listId = e.currentTarget.dataset.lid;
			uni.navigateTo({
				url: '/pages/detail/detail?lid='+listId
			})
		},
		// 点赞
		givePraise: function (e) {
			console.log(e);
			// 操作失败
		},
		// 用户中心
		userCenter: function(e) {
			if (this.loginCheck()) {
				var userId = e.currentTarget.dataset.uid;
				uni.navigateTo({
					url: '/pages/user/user?uId='+userId
				})
			} else {
				console.log('用户未登录');
			}
		},
		// 打开评论页
		openComment: function(e) {
			if (this.loginCheck()) {
				var articleId = e;
				uni.navigateTo({
					url: '/pages/commentList/commentList?aId='+articleId
				})
			} else {
				console.log('用户未登录');
			}
		},
		// 显示分享
		openShare: function(e) {
			this.shareType = true
			console.log('显示')
		},
		// 退出分享
		exitShare: function(e) {
			this.shareType = false
		},
		// 登录验证
		loginCheck: function () {
			var loginStatus = uni.getStorageSync('Login_status');
			if (loginStatus == '') {
				uni.navigateTo({
					url: '/pages/login/login'
				})
				uni.showToast({
					icon: 'none',
					title: '请先登录!'
				})
				return false;
			}
			return true;
		}
	},
	computed: {
		scrollLeft() {
			return (this.tabIndex - 1) * 60
		},
		// 时间区间判断
		getDateDiff() {
			return function (timeStamp) {
				var minute = 1000 * 60;
				var hour = minute * 60;
				var day = hour * 24;
				var halfamonth = day * 15;
				var month = day * 30;
				var now = new Date().getTime();
				var diffValue = now - timeStamp;
				if(diffValue < 0){return;}
				var monthC =diffValue/month;
				var weekC =diffValue/(7*day);
				var dayC =diffValue/day;
				var hourC =diffValue/hour;
				var minC =diffValue/minute;
				var result = '';
				if(monthC>=1){
					result="" + parseInt(monthC) + "个月前";
				}
				else if(weekC>=1){
					result="" + parseInt(weekC) + "周前";
				}
				else if(dayC>=1){
					result=""+ parseInt(dayC) +"天前";
				}
				else if(hourC>=1){
					result=""+ parseInt(hourC) +"小时前";
				}
				else if(minC>=1){
					result=""+ parseInt(minC) +"分钟前";
				}else {
					result="刚刚";
				}
				return result;
			}
		}
	}
	// onReady: function () {
